//404 Page Not Found screen
export function Error404() {
    const history = useHistory()
    const transitionDuration = {
        enter: 300,
        exit: 0,
      };
    return (
        <div style={{ width: "100%", marginTop: "4rem", height: "100%", display: "flex", justifyContent: "center", alignItems: "center" }}>
            <div style={{ display: "flex", justifyContent: "center", alignItems: "center", flexDirection: "column" }}>
                <CarCrash width="20rem" />
                <h1><span style={{ fontSize: "40px", fontWeight: "600", color: "red" }}>404:&nbsp;</span>Page Not found</h1>
                <p>You have tried to reach a page that is not currently part of the application</p>
                <Zoom
                    in={true}
                    timeout={transitionDuration}
                    style={{
                        transitionDelay: `${transitionDuration.exit}ms`,
                    }}
                >
                                    <Fab color="primary" onClick={() => history.push(`${process.env.PUBLIC_URL}`)}><Home /></Fab>

                </Zoom>
            </div>
        </div>)
}
